<template>
  <div class="chat-container">
    <van-nav-bar
      class="page-navbar"
      title="我不是robot"
      fixed
    />
    <div class="message-list" ref="message-list">
      <div
        class="message-item"
        :class="{ reverse: index%2 == 1 }"
        v-for="(item, index) in messages"
        :key="index"
      >
        <van-image
          class="avatar"
          round
          fit="cover"
          :src="photo(index)"
        />
        <div class="msg-wrap" style="flex: 1;">
          <div class="title">{{ item }}</div>
        </div>
      </div>
    </div>
    <van-cell-group class="send-message">
      <van-field
        center
        clearable
        v-model="message"
      >
        <van-button
          slot="button"
          size="mini"
          @click="onSend"
          type="primary"
        >发送</van-button>
      </van-field>
    </van-cell-group>
  </div>
</template>

<script>
import axios from 'axios'
import { getItem, setItem } from '@/utils/storage'
import { getUserProfile } from '@/api/user'
import { mapState } from 'vuex'
export default {
  name: 'ChatPage',
  data () {
    return {
      messages: getItem('messages') || ['你好'],
      message: '',
      robotPhoto: 'https://img01.yzcdn.cn/vant/logo.png',
      userphoto: ''
    }
  },
  computed: {
    ...mapState(['user'])
  },
  watch: {},
  created () {
    this.loadCurrentUser()
  },
  mounted () {
    this.scrollToBottom()
  },
  methods: {
    photo (index) {
      if (index % 2 === 1) {
        return this.userphoto
      }
      return this.robotPhoto
    },
    async loadCurrentUser () {
      const { data } = await getUserProfile()
      this.userphoto = data.data.photo
    },
    onSend () {
      this.scrollToBottom()
      const message = this.message.trim()
      if (!message) {
        return
      }
      this.onloadAxios()
      this.messages.push(this.message)
      this.message = ''
    },
    async onloadAxios () {
      try {
        const res = await axios.post(
          `http://www.tuling123.com/openapi/api?key=61d2ef6a5c38407f8d58df05ea992df7&info=${this.message}`
        )
        this.messages.push(res.data.text)
      } catch (error) {
        this.messages.push('你网咯不好,不想和你说话')
      }
      setItem('messages', this.messages)
    },
    scrollToBottom () {
      const messageList = this.$refs['message-list']
      this.$nextTick(() => {
        messageList.scrollTop = messageList.scrollHeight
      })
    }
  }
}
</script>

<style scoped lang="less">
.chat-container {
  position: absolute;
  width: 100%;
  height: 94%;
  padding: 46px 0 50px 0;
  top: 0;
  left: 0;
  box-sizing: border-box;
  background: #f5f5f6;
  .message-list {
    height: 100%;
    overflow-y: scroll;
    .message-item {
      display: flex;
      flex-flow: row nowrap;
      align-items: flex-start;
      padding: 10px;
      .msg-wrap {
        word-break: break-all;
        word-wrap: break-word;
      }
      .title {
        display: table;
        background: #e0effb;
        border-radius: 6px;
        font-size: 14px;
        padding: 10px 10px;
        margin-right: 45px;
      }
      .avatar {
        width: 40px;
        height: 40px;
        margin-right: 5px;
      }
    }
    .reverse {
      flex-direction: row-reverse;
      .msg-wrap {
        display: flex;
        flex-direction: row-reverse;
        .title {
          margin-right: 5px;
          margin-left: 45px;
        }
      }
    }
  }

  .send-message {
    position: fixed;
    bottom: 40px;
    left: 0;
    right: 0;
    background: #f5f5f6 !important;
  }
}
</style>
